<template>
  <div class="row mb-12 mt-12">
    <div class="col">
      <h4>{{time | hour}}</h4>
      <h6>{{time | month}}</h6>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      time: new Date().toISOString(),
      interval: null,
    };
  },
  mounted() {
    this.interval = setInterval(() => {
      this.time = new Date().toISOString();
    }, 1000);
  },
  unmounted() {
    clearInterval(this.interval);
  },
};
</script>

