<template>
  <div>
    <Clock />
    <div class="row mt-12">
      <div class="col-md-8">
        <router-view />
      </div>
      <div class="col-md-4 mt-4">
        <div
          :class="{'selected': group.selected}"
          @click="selectGroup(group)"
          class="group text-left mb-2"
          v-for="group in groups"
          :key="group.id"
        >
          {{group.name}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Clock from '@/components/Clock.vue';
import router from '../router';

export default {
  name: 'my-component',
  methods: {
    selectGroup(group) {
      this.groups.forEach((g) => {
        g.selected = false;
      });
      group.selected = true;
      router.push(`/standings/groups/${group.id}`);
    },
  },
  watch: {
    $route: {
      immediate: true,
      handler(route) {
        if (route.path === '/standings') {
          router.push(`/standings/groups/${this.groups[0].id}`);
        }
      },
    },
    '$route.params': {
      immediate: true,
      handler({ groupId }) {
        if (groupId) {
          const group = this.groups.find(g => g.id === parseInt(groupId, 10));
          this.selectGroup(group);
        }
      },
    },
  },
  data() {
    return {
      groups: [
        {
          id: 1,
          name: 'Overall Standings',
        },
        {
          id: 2,
          name: 'Custom Group 1 Standings',
        },
        {
          id: 3,
          name: 'Custom Group 2 Standings',
        },
      ].map(group => ({ ...group, selected: false })),
    };
  },
  components: {
    Clock,
  },
};
</script>

<style scoped>
.group {
  cursor: pointer;
}

.selected {
  font-weight: bold;
}
</style>
