<template>
  <div>
    <vue-good-table
      :columns="columns"
      :rows="groups"
      @on-row-click="onRowClick"
      :search-options="{
        enabled: false,
      }"
      :pagination-options="{
        enabled: false,
      }"
      styleClass="vgt-table"
    >
    </vue-good-table>
    <div class="row mt-8">
      <div class="col-md-3"></div>
      <div class="text-center col-md-6">
        <input
          placeholder="Search Group Name"
          v-model="search"/>
      </div>
      <div class="col-md-3"></div>
    </div>

    <Modal v-if="groupToJoin">
      <h3 slot="header" class="text-center">Confirmation</h3>
      <div slot="body">
        <h6 class="normal mb-4">Are you sure you want to join this group?</h6>

        <h3 class="mb-4">{{groupToJoin.name}}</h3>

        <div class="row">
          <div class="col-md-6">
            <h6 class="normal">Start Date:</h6>
            <h6 class="normal">End Date:</h6>
            <h6 class="normal">Group Size:</h6>
          </div>
          <div class="col-md-6">
            <h6 class="normal">{{groupToJoin.startDate}}</h6>
            <h6 class="normal">{{groupToJoin.endDate}}</h6>
            <h6 class="normal">{{groupToJoin.maxPlayers}}</h6>
          </div>
        </div>
      </div>
      <div slot="footer">
        <button class="mr-4" @click="onJoin">YES</button>
        <button @click="onNoJoin">NO</button>
      </div>
    </Modal>
  </div>
</template>

<script>
import Modal from '@/components/Modal.vue';

export default {
  name: 'my-component',
  methods: {
    onNoJoin() {
      this.groupToJoin = null;
    },
    onJoin() {
      this.groupToJoin = null;
    },
    onRowClick(params) {
      this.groupToJoin = params.row;
    },
  },
  components: {
    Modal,
  },
  computed: {
    groups() {
      return this.rows.filter(group => this.search === '' || group.name.toUpperCase().indexOf(this.search.toUpperCase()) !== -1);
    },
  },
  data() {
    return {
      search: '',
      groupToJoin: null,
      columns: [
        {
          label: 'Group Name',
          field: 'name',
        },
        {
          label: '# of Players',
          field: 'numberOfPlayers',
          tdClass: 'text-center',
          thClass: 'text-center',
        },
        {
          label: 'Join Status',
          field: 'joinStatus',
          tdClass: 'text-center',
          thClass: 'text-center',
        },
        {
          label: 'Start Date',
          field: 'startDate',
          tdClass: 'text-center',
          thClass: 'text-center',
        },
        {
          label: 'End Date',
          field: 'endDate',
          tdClass: 'text-center',
          thClass: 'text-center',
        },
      ],
      rows: [
        {
          id: 1,
          name: 'Paradoxial Issue Group',
          numberOfPlayers: 23,
          joinStatus: 'public',
          maxPlayers: 24,
          startDate: new Date().toISOString(),
          endDate: new Date().toISOString(),
        },
        {
          id: 1,
          name: 'Some Other Group',
          numberOfPlayers: 10,
          maxPlayers: 24,
          joinStatus: 'private',
          startDate: new Date().toISOString(),
          endDate: new Date().toISOString(),
        },
      ].map((player, i) => ({
        ...player,
        startDate: this.$options.filters.month(player.startDate),
        endDate: this.$options.filters.month(player.endDate),
        position: i + 1,
      })),
    };
  },
};
</script>

